body {
  background-color: black;
  color: white;
}

.App {
  text-align: center;
}

h1 {
  color: burlywood;
  border-bottom: #ffdeda 2px solid;
  margin-bottom: 1%;
  margin-top: 1%;
}

h2 {
  color: darkgrey;
  font-family: cursive;
  padding: 1% 0;
}

.main-content {
  display: flex;
  justify-content: space-between;
  font-size: x-large;
}

.section1 {
  flex: 1;
}

.section2 {
  flex: 1;
}

.section3 {
  flex: 1;
}

.model-options-container {
  max-width: fit-content;
  margin-left: auto;
  margin-right: auto;
  font-size: large;
}

.model-options {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: end;
}

.model-options label {
  padding: 5% 0;
  cursor: pointer;
  font-size: larger;
}

.image-upload-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-left: 2px solid rgb(87, 84, 84);
  border-right: 2px solid rgb(87, 84, 84);
  min-height: 50%;
}

.image-upload-container img {
  padding: 5% 0;
}

.result-container {
  max-width: fit-content;
  min-width: 40%;
  margin-left: auto;
  margin-right: auto;
  background-color: darkgrey;
  border-radius: 5px;
  padding: 1% 1%;
  margin-bottom: 4%;
  color: black;
}

.result-container.loading {
  background-color: black;
}

.result1 {
  text-align: left;
  border-bottom: 2px solid black;
}

.result2 {
  font-size: x-large;
  font-weight: bold;
}

.button-56 {
  align-items: center;
  background-color: #fee6e3;
  border: 2px solid darkgray;
  border-radius: 8px;
  box-sizing: border-box;
  color: #111;
  cursor: pointer;
  display: flex;
  font-family: Inter, sans-serif;
  font-size: 16px;
  height: 48px;
  justify-content: center;
  line-height: 24px;
  max-width: 100%;
  padding: 0 25px;
  position: relative;
  text-align: center;
  text-decoration: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  margin: auto;
  margin-bottom: 20%;
}

.button-56:after {
  background-color: #fff;
  border-radius: 8px;
  content: "";
  display: block;
  height: 48px;
  left: 0;
  width: 100%;
  position: absolute;
  top: -2px;
  transform: translate(8px, 8px);
  transition: transform 0.2s ease-out;
  z-index: -1;
}

.button-56:hover:after {
  transform: translate(0, 0);
}

.button-56:active {
  background-color: #ffdeda;
  outline: 0;
}

.button-56:hover {
  outline: 0;
}

@media (min-width: 768px) {
  .button-56 {
    padding: 0 40px;
  }
}

.loader {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  display: inline-block;
  position: relative;
  border: 3px solid;
  border-color: #fff #fff transparent transparent;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
}
.loader::after,
.loader::before {
  content: "";
  box-sizing: border-box;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  border: 3px solid;
  border-color: transparent transparent #ff3d00 #ff3d00;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  box-sizing: border-box;
  animation: rotationBack 0.5s linear infinite;
  transform-origin: center center;
}
.loader::before {
  width: 32px;
  height: 32px;
  border-color: #fff #fff transparent transparent;
  animation: rotation 1.5s linear infinite;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes rotationBack {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-360deg);
  }
}

@media (max-width: 1200px) {
  .main-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    font-size: x-large;
  }

  .image-upload-container {
    border-left: none;
    border-right: none;
  }
}

.healthy {
  color: green;
}

.rotten {
  color: red;
}

.unknown {
  color: black;
}
